body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  align-items: center;
  justify-content: center;
}

.navbar {
  background-color: black;
  color: rgb(61, 209, 61);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px;
  text-align: center;
}

.navbar img {
  height: 40px;
  margin-right: 10px;
}


.title{
  text-align: center;
}

.form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.form-container input,
.form-container button {
  border: 2px solid #ccc;
  width: 100%;
  padding: 10px;
  
  margin: 5px;
  border-radius: 5px;
}

.form-container button {
  background-color: rgb(15, 108, 15);
  color: white;
  font-size: large;
  font-weight: bolder;
  margin-top: 12px;
  cursor: pointer;
  border: 2px solid rgb(15, 108, 15);
  border-radius: 12px;
}

.form-container button:hover {
  background-color: rgb(24, 129, 24);
}


.items-container {
  margin-top: 20px;
  padding: 10px;
  border: 2px solid #ccc;
  border-radius: 5px;
  width: 300px;
  margin-left: 40%;
  align-items: center;
  text-align: center;
}

.items-container ul {
  list-style-type: none;
  padding: 0;
  font-size: 25px;
  font-weight: 400;
}

.items-container li {
  padding: 5px 0;
  border-bottom: 1px solid #ccc;

}

.items-container li:last-child {
  border-bottom: none;
}